@import '../common.sass'

.container 
  display: block
  position: relative


.appbar
  @include appbar


.pane
  display: flex
  justify-content: center
  align-items: center
  flex-direction: column
  height: calc(100vh - 3rem)


.select-title
  display: flex
  justify-content: center
  align-items: center
  margin: 0 0 5rem
  font-size: 2rem


.level-list
  display: flex
  justify-content: space-around
  align-items: center
  flex-direction: column


.level-button
  @include accent-button
  width: 7em
